.navbar-wrapper::before {
  content: '';
  position: absolute;

  inset: -2px;
  z-index: -1;
  border-radius: inherit;

  background: linear-gradient(to top, #e8e8e8, #f7f7f7);
}

.blur-component {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(2px);
  mask-image: linear-gradient(180deg, #0000 0%, #000 30%);
}

